    import cj from "@/common/crypto-js.js"
	export default {
		data() {
			return {
				index: 0,
                username: '',
                phoneNum: '',
                password: '',
                smsCode: '',
                userFalg: false,
                phoneFalg: false,
                codeFalg: false,
                pwdFalg: false,
                isShowPwd: true,
                sendCodeText: '发送验证码',
                timeText: '',
                num: 60,
                codeTime: '',
                beginFalg: false,
                errorText: "",
                errorFlag: false,
                userobj: {},
                imgObj: {}
			}
		},
		methods: {
			pwd(){
                this.index = 0;
            },
            code(){
                this.index = 1;
            },
            swiperChange(e){
                this.index = e.detail.current;
            },
            checkUser(){
                let phone_regExp = /^1[3-9]\d{9}$/;
                if (this.username != '' && phone_regExp.test(this.username)) {
                    this.userFalg = true;
                    return true
                } else {
                    uni.showToast({
                        title: '请检查手机号',
                        icon: 'none',
                        mask: true
                    });
                    setTimeout(function(){
                        uni.hideToast();
                    },2000)
                    this.userFalg = false;
                    return false
                }
            },
            checkPhone(){
                let phone_regExp = /^1[3-9]\d{9}$/;
                if (this.phoneNum != '' && phone_regExp.test(this.phoneNum)) {
                    this.phoneFalg = true;
                    return true
                } else {
                    uni.showToast({
                        title: '请检查手机号',
                        icon: 'none',
                        mask: true
                    });
                    setTimeout(function(){
                        uni.hideToast();
                    },2000)
                    this.phoneFalg = false;
                    return false
                }
            },
            checkPwd(){
                let pwd_regExp = /^(?![a-zA-Z]+$)(?![0-9]+$)[A-Za-z0-9]{6,15}$/;
                if (this.password != '' && pwd_regExp.test(this.password)) {
                    this.pwdFalg = true;
                    return true
                } else {
                    uni.showToast({
                        title: '请输入6-15位的字母数字组合',
                        icon: 'none',
                        mask: true
                    });
                    setTimeout(function(){
                        uni.hideToast();
                    },2000)
                    this.pwdFalg = false;
                    return false
                }
            },
            checkCode(){
                if (this.smsCode != '') {
                    this.codeFalg = true;
                    return true
                } else {
                    this.codeFalg = false;
                    uni.showToast({
                        title: '验证码不能为空',
                        icon: 'none',
                        mask: true
                    });
                    setTimeout(function(){
                        uni.hideToast();
                    },2000)
                    return false
                }
            },
            showPass(){
                if (this.isShowPwd) {
                    this.isShowPwd = false;
                    return false
                } else {
                    this.isShowPwd = true;
                    return true
                }
            },
            login(){
                if (this.index === 0) {
                    if (this.checkUser() && this.checkPwd()) {
                        let pwdStr = cj.MD5(this.password).toString();
                        let nameStr = new Buffer(this.username).toString('base64');
                        uni.request({
                            url: this.websiteUrl + '/api/login',
                            method: 'POST',
                            data: {
                                mobile: nameStr,
                                userPwd: pwdStr
                            },
                            success: res => {
                                if (res.data && res.data.status === 0) {
                                    let userData = res.data;
                                    let phonestr = new Buffer(userData.user.userPhone,'base64').toString('utf8');
                                    this.userobj.userPhone = phonestr;
                                    this.userobj.userName = userData.user.userNickName;
                                    this.userobj.userCode = userData.user.userInvite;
                                    this.userobj.userId = parseInt(userData.user.userId);
                                    this.userobj.loginToken = userData.token;
                                    if (userData.user.userBlurb == null) {
                                        this.userobj.userText = '';
                                    } else{
                                        this.userobj.userText = userData.user.userBlurb;
                                    }
                                    if (userData.user.userPortrait == '') {
                                        this.imgObj.networkImage = ''
                                        this.imgObj.localImage = '/static/140.png';
                                        this.$store.commit('storeImage',this.imgObj);
                                    } else{
                                        this.imgObj.networkImage = this.websiteUrl + '/' + userData.user.userPortrait;
                                        let that = this;
                                        uni.downloadFile({
                                            url: that.imgObj.networkImage,
                                            success: function (res) {
                                                let tempFilePath = res.tempFilePath;
                                                uni.saveFile({
                                                    tempFilePath: tempFilePath,
                                                    success: function (res) {
                                                        let savedFilePath = res.savedFilePath;
                                                        that.imgObj.localImage = savedFilePath;
                                                        that.$store.commit('storeImage',that.imgObj);
                                                    }
                                                })
                                            }
                                        })
                                    }
                                    this.$store.commit('login',this.userobj);
                                    uni.navigateBack();
                                } else {
                                    uni.showToast({
                                        title: res.data.message,
                                        icon:'none',
                                        mask: true
                                    });
                                    setTimeout(function(){
                                        uni.hideToast();
                                    },2000)
                                }
                            },
                            fail: () => {},
                            complete: () => {}
                        });
                    } else {
                        return false
                    }
                } else{
                    if (this.checkPhone() && this.checkCode()) {
                        let phoneStr = new Buffer(this.phoneNum).toString('base64');
                        uni.request({
                            url: this.websiteUrl + '/api/loginsms',
                            method: 'POST',
                            data: {
                                mobile: phoneStr,
                                smsCode: this.smsCode
                            },
                            success: res => {
                                if (res.data && res.data.status === 0) {
                                    let userData = res.data;
                                    let phonestr = new Buffer(userData.user.userPhone,'base64').toString('utf8');
                                    this.userobj.userPhone = phonestr;
                                    this.userobj.userName = userData.user.userNickName;
                                    this.userobj.userCode = userData.user.userInvite;
                                    this.userobj.userId = parseInt(userData.user.userId);
                                    this.userobj.loginToken = userData.token;
                                    if (userData.user.userBlurb == null) {
                                        this.userobj.userText = '';
                                    } else{
                                        this.userobj.userText = userData.user.userBlurb;
                                    }
                                    if (userData.user.userPortrait == '') {
                                        this.imgObj.networkImage = ''
                                        this.imgObj.localImage = '/static/140.png';
                                        this.$store.commit('storeImage',this.imgObj);
                                    } else{
                                        this.imgObj.networkImage = this.websiteUrl + '/' + userData.user.userPortrait;
                                        let that = this;
                                        uni.downloadFile({
                                            url: that.imgObj.networkImage,
                                            success: function (res) {
                                                let tempFilePath = res.tempFilePath;
                                                uni.saveFile({
                                                    tempFilePath: tempFilePath,
                                                    success: function (res) {
                                                        let savedFilePath = res.savedFilePath;
                                                        that.imgObj.localImage = savedFilePath;
                                                        that.$store.commit('storeImage',that.imgObj);
                                                    }
                                                })
                                            }
                                        })
                                    }
                                    this.$store.commit('login',this.userobj);
                                    uni.navigateBack();
                                } else {
                                    uni.showToast({
                                        title: res.data.message,
                                        icon: 'none',
                                        mask: true
                                    });
                                    setTimeout(function(){
                                        uni.hideToast();
                                    },2000)
                                }
                            },
                            fail: () => {},
                            complete: () => {}
                        });
                    } else {
                        return false
                    }
                }
            },
            sendCode(){
                let phone_regExp = /^1[3-9]\d{9}$/;
                if (this.phoneNum == '' || !phone_regExp.test(this.phoneNum)) {
                    uni.showToast({
                        title: '请检查手机号',
                        icon: 'none',
                        mask: true
                    });
                    setTimeout(function(){
                        uni.hideToast();
                    },2000)
                    return false;
                }
                this.codeTime = setInterval(this.countDown,1000);
                let phoneStr = new Buffer(this.phoneNum).toString('base64');
                uni.request({
                    url: this.websiteUrl + '/api/code/sms',
                    method: 'POST',
                    data: {
                        mobile: phoneStr
                    },
                    success: res => {
                        console.log(res)
                    },
                    fail: () => {},
                    complete: () => {}
                });
            },
            countDown(){
                this.num--;
                this.beginFalg = true;
                this.timeText = this.num + 's';
                if (this.num <= 0) {
                    this.num = 60;
                    this.timeText = '';
                    this.beginFalg = false;
                    clearInterval(this.codeTime)
                }
            }
		}
	}
